import AMPComponent from "./AMPComponent.js"
import Utils from "./Utils.js"

export default class Duration extends AMPComponent {
  
  constructor(props) {
    super(props)
    this.state.duration = props.player.duration
  }
  
  ondurationchange() {
    this.setState({duration: Math.round(this.player.duration)})
  }
  
  render() {
    console.log("Duration.render")
    return <span className="amp-duration">{Utils.formatTimecode(this.state.duration, this.state.duration)}</span>
  }
  
}