import Panel from "./Panel.js"

export default class Bitrate extends Panel {
  
  constructor(props) {
    super(props)
    this.listOptions = []
    this.bitrateArray = []
    this.state.activeIndex = -1
    this.player.addEventListener("bitratelevelsloaded", this.onBitrateLoaded.bind(this))
    this.player.addEventListener("bitratechanging", this.onBitrateChange.bind(this))
    
  }
  
  componentWillMount() {
    this.props.bitrate.bitrateArray && this.onBitrateLoaded()
    this.props.bitrate.bitrateIndex !== undefined && this.onBitrateChange()
    this.open = true
   }
  
  clickHandler(object, index) {
    this.player.setQuality(index)
  }
  
  onBitrateLoaded(data) {
    this.bitrateArray = data? data.data.levels : this.props.bitrate.bitrateArray
    for (let item of this.bitrateArray) {
      this.listOptions.push({"label": item.bitrate})
    }
    this.setState({redraw: true})
  }
  
  onBitrateChange(data) {
    let autoLevel = false
    try {
      autoLevel = this.player.playerCore.activePlaybackCore.player.autoLevelEnabled === true
    } catch (e) {
    } 
    
    let index = autoLevel? -1 : (data? this.bitrateArray.indexOf(data.data) : this.props.bitrate.bitrateIndex)
    
    this.setState({activeIndex: index})
  }
  
  render() {
    
    var OptionsList = this.listOptions.map(function(object,index){

      return <div key={index}  className="amp-list-item" onClick={this.clickHandler.bind(this, object, index)}>
              <button className={index === this.state.activeIndex ? 'amp-icon amp-list-item-selected':'amp-icon'}></button>
              <span>{object.label/1000} Kbps</span>
             </div>
    }, this);
    
    
    return <div ref="element">
            <div className="amp-list-item amp-list-title" onClick={this.props.gotoSettings.bind(this)}>
              <button className="amp-icon amp-icon-left"></button>
              <span>Quality</span>
            </div>
            <div className="amp-list-item" onClick={this.clickHandler.bind(this, null, -1)}>
              <button className={-1 === this.state.activeIndex ? 'amp-icon amp-list-item-selected':'amp-icon'}></button>
              <span>Auto</span>
            </div>
            {OptionsList}
           </div>
  }
} 