import Panel from "./Panel.js"
import CaptionSettings from "./CaptionSettings.js"
import Bitrate from "./Bitrate.js"

export default class Settings extends Panel {
  
  constructor(props) {
    super(props)
    this.state = {
      settingsLevel: 0,
      bitrateLabel: "Auto"
    }
    this.player.captioning.addEventListener("trackselected", this.trackselected.bind(this))
    this.player.captioning.addEventListener("tracksloaded", this.tracksloaded.bind(this))
    this.player.addEventListener("bitratechanging", this.bitrateChange.bind(this))
    this.player.addEventListener("bitratelevelsloaded", this.bitratesLoaded.bind(this))
  }
  
  
  trackselected(track)
  {
    let lang = this.props.player.localization.getLanguageString(track.detail.language) || track.detail.language || "None"
    this.setState({language: lang})
  }
  
  tracksloaded(data)
  {
    this.state.captioningTracks = data.detail
  }
  
  bitratesLoaded(event)
  {
    this.state.bitrateArray = event.data.levels
  }
  
  bitrateChange(event)
  {
    let brLabel = event.detail.bitrate ? (event.detail.bitrate/1000 + "Kbps") : "Auto"
    this.state.bitrateIndex = this.state.bitrateArray? this.state.bitrateArray.indexOf(event.detail) : -1
    this.setState({bitrateLabel: brLabel})
  }
  
  changeState(value) {
    setTimeout(() => { this.setState({settingsLevel:value});}, 100)
  }
  
  render() {
    switch (this.state.settingsLevel) {
      case 0:
      return (<div ref="element" {...this.propsList} className={`amp-settings amp-panel ${this.className}`}>
              <div className='amp-list-item' onClick={this.changeState.bind(this, 1)}>
                <span className="amp-label">Subtitles</span>
                <button className="amp-icon amp-icon-right amp-right"></button>
                <span className="amp-right">{this.state.language}</span>
              </div>
              <div className='amp-list-item' onClick={this.changeState.bind(this, 2)}>
                <span className="amp-label">Bitrate</span>
                <button className="amp-icon amp-icon-right amp-right"></button>
                <span className="amp-right">{this.state.bitrateLabel}</span>
              </div>
             </div>)
        break
      case 1:
        return (<div ref="element" className={`amp-captioning-settings amp-panel ${this.className}`}>
                  <CaptionSettings captionTracks={this.state.captioningTracks}  player={this.player} gotoSettings={this.changeState.bind(this,0)}/>
                </div>)
        break
      case 2:
        return (<div ref="element" className={`amp-bitrate amp-panel ${this.className}`}>
                  <Bitrate bitrate={this.state} player={this.player} gotoSettings={this.changeState.bind(this,0)} />
                </div>)
        break  
    }
    
  }
} 