import AMPComponent from "./AMPComponent.js"
import AMPContainer from "./AMPContainer.js"
import AMPPanelControl from "./AMPPanelControl.js"
import PlayPause from "./PlayPause.js"
import PauseOverlay from "./PauseOverlay.js"
import Rewind from "./Rewind.js"
import CurrentTime from "./CurrentTime.js"
import Duration from "./Duration.js"
import Progress from "./Progress.js"
import Volume from "./Volume.js"
import Mute from "./Mute.js"
import Fullscreen from "./Fullscreen.js"
import Panel from "./Panel.js"
import PlaybackRate from "./PlaybackRate.js"
import Bitrate from "./Bitrate.js"
import Settings from "./Settings.js"
import CaptionSettings from "./CaptionSettings.js"
import Share from "./Share.js"
import CaptionToggleButton from "./CaptionToggleButton.js"

export default class UI extends AMPComponent {
  constructor(props) {
    super(props)
    this._playstate = "ready"
    this._activeLoced = false
    this.state.media = {}
    this.timeout = null
  }
  
  get container() {
    return this.refs.container
  }
  
  get components() {
    return this.refs
  }
  
  set playstate(value) {
    if (this._playstate == value)
      return
      
    const updates = {
      [`amp-${this._playstate}`]: false,
      [`amp-${value}`]: true
    }
    this._playstate = value
    this.container.classList.update(updates)
  }
  
  get playstate() {
    return this._playstate
  }
  
  set waiting(value) {
    if (this._waiting == value)
      return
    this._waiting = value  
    this.container.classList.update({"amp-waiting": this._waiting})
  }
  
  onmediachange(event) {
    const media = this.player.media
    this.container.classList.update({"amp-text-tracks": media.tracks && media.tracks.length})
    this.setState({media})
  }

  onplaystatechange(event) {
    const state = event.detail.value
    if (state == "waiting")
      return
    this.playstate = state
  }
  
  onplaying() {
    this.waiting = false
  }
  
  onseeking() {
    this.waiting = true
  }
  
  onseeked() {
    this.waiting = false
  }
  
  onwaiting() {
    this.waiting = true
  }
  
  togglePlayPause() {
    if (this.player.paused) {
      this.player.play()
    } else {
      this.player.pause()
    }
  }
  
  mouseenter() {
    clearTimeout(this.timeout)
    this.container.classList.update({"amp-active": true, "amp-inactive": false})
  }
  
  mouseleave() {
    if (this.activeLocked == true || this.playstate == "ready" || this.playstate == "paused")
      return
      
    this.container.classList.update({"amp-active": false, "amp-inactive": true})
    this.timeout = setTimeout(() => {
      this.container.classList.update({"amp-inactive": false})
    }, this.config.autoHide * 1000)
  }
  
  render() {
    console.log("UI.render")
    return (
      <AMPContainer ref="container" className="amp-ui" classList={["amp-ready", "amp-active"]} 
          onMouseEnter={() => this.mouseenter()} onMouseLeave={() => this.mouseleave()}
          onFocus={(event)=>console.log(event.type, event)}
          onBlur={(event)=>console.log(event.type, event)}>
        <div className="amp-interactive" onClick={() => this.togglePlayPause() } tabIndex="-1" />
        <img className="amp-poster" src={this.state.media.poster} tabIndex="-1" />
        <PauseOverlay ref="pauseOverlay" altText="MSG_PLAY" player={this.player} />
        <Progress ref="progress" player={this.player} />
        <AMPContainer ref="controls" className="amp-controls">
          <PlayPause ref="playpause" altText="MSG_PLAY" player={this.player} />
          <Rewind ref="rewind" altText="MSG_REWIND" player={this.player} />
          <div ref="timeDisplay" className="amp-time-display">
             <CurrentTime ref="currentTime" player={this.player} />
             <span ref="timeSeparator" className="amp-time-separator" />
             <Duration ref="duration" player={this.player} />
          </div>
          <Mute ref="mute" altText="MSG_MUTE" player={this.player} />
          <Volume ref="volume" altText="MSG_VOLUME" player={this.player} />
          <div ref="spacer1" className="amp-spacer" />
          <div ref="spacer2" className="amp-spacer" />
          <PlaybackRate ref="playbackrate" altText="MSG_PLAYBACK_RATE" player={this.player} />
          <AMPPanelControl ref="share" altText="MSG_SHARE" className="amp-share" player={this.player} panel={this.refs.sharePanel} />
          <CaptionToggleButton ref="cc" altText="MSG_CC" className="amp-cc" player={this.player}/>
          <AMPPanelControl ref="settings" altText="MSG_SETTINGS" className="amp-settings" player={this.player} panel={this.refs.settingsPanel} />
          <Fullscreen ref="fullscreen" altText="MSG_ENTER_FULLSCREEN" player={this.player} />
        </AMPContainer>
        <Share ref="sharePanel" player={this.player} />
        <Settings ref="settingsPanel" player={this.player} />
      </AMPContainer>
    )
  }
  
  destroy() {
    ReactDOM.unmountComponentAtNode(this._container)
  }
  
  static createElement(player) {
    const element = document.createElement("div")
    element.className = "amp-react"
    const ready = () => {
      player.getViewComponent().appendChild(element)
      player.removeEventListener("ready", ready)
    }
    player.addEventListener("ready", ready)
    return element
  }
  
  static create(player, config) {
    return new Promise((resolve, reject) => {
      const element = UI.createElement(player)
      resolve(
        ReactDOM.render(<UI player={player} config={config} />, element)
      )
    })
  }
}