import Slider from "./Slider.js"

export default class Volume extends Slider {
  
  constructor(props) {
    super(props)
    this.classList.add("amp-volume")
    this.state.value = this.player.volume
  }
  
  onvolumechange() {
    this.setState({value: this.player.volume})
  }
  
  valueChanged(value) {
    this.player.volume = value
  }
}