import Panel from "../Panel.js"

export default class FontFamily extends Panel {
  
  constructor(props) {
    super(props)
    this.listOptions = [{label:"Monospaced Serif", value: "'Courier New', Courier, 'Nimbus Mono L', 'Cutive Mono', monospace"},
                         {label:"Proportional Serif", value: "'Times New Roman', Times, Georgia, Cambria, 'PT Serif Caption', serif"},
                         {label:"Monospaced Sans-Serif", value: "'Deja Vu Sans Mono', 'Lucida Console', Monaco, Consolas, 'PT Mono', monospace"},
                         {label:"Proportional Sans-Serif", value: "Roboto, 'Arial Unicode Ms', Arial, Helvetica, Verdana, 'PT Sans Caption', sans-serif"},
                         {label:"Casual", value: "'Comic Sans MS', Impact, Handlee, fantasy"},
                         {label:"Cursive", value: "'Monotype Corsiva', 'URW Chancery L', 'Apple Chancery', 'Dancing Script', cursive"},
                         {label:"Small Capitals", value: "'Arial Unicode Ms', Arial, Helvetica, Verdana, 'Marcellus SC', sans-serif; font-variant: small-caps"}
                        ];
    this.listOptions.map(function(object, index){ 
      if(object.label === props.captionsettings['fontFamily']) {
        this.state.activeIndex = index
      }
    },this)    

  }
  
  clickHandler (object, index) {
    this.setState({activeIndex: index})
    this.props.settingsChange({"fontFamily": { "label":object.label , "value":object.value}})
  }
  
  render() {
    
    var OptionsList = this.listOptions.map(function(object, index){

      return <div key={index} className="amp-list-item" onClick={this.clickHandler.bind(this, object, index)}>
              <button className={index === this.state.activeIndex ? 'amp-icon amp-list-item-selected':'amp-icon'}></button>
              <span>{object.label}</span>
             </div>
    }, this);
    
    return <div>
            <div className="amp-list-item amp-list-title" onClick={this.props.viewChange.bind(this, "options")}>
              <button className="amp-icon amp-icon-left"></button>
              <span>Font Family</span>
            </div>
            {OptionsList}
           </div>
    
    
  }
}
