import Slider from "./Slider.js"

export default class Progress extends Slider {
  
  constructor(props) {
    super(props)
    this.classList.add("amp-progress")
  }
  
  updateValue() {
    this.setState({value: this.player.currentTime / this.player.duration})
  }
  
  ontimeupdate() {
    this.updateValue()
  }
  
  ondurationchange() {
    this.increment = 10 / this.player.duration
    this.updateValue()
  }
  
  valueChanged(value) {
    this.player.currentTime = value * this.player.duration
  }
}