import Panel from "./Panel.js"

export default class Share extends Panel {
  
  constructor(props) {
    super(props)
    this.listOptions = [
                         {label:"Facebook", icon: "amp-fb", url:"www.facebook.html"},
                         {label:"Twitter", icon: "amp-tw", url:""}
                       ]
  }
  
  
  clickHandler(object, index) {
    if(confirm("Would you like to share on:"+object.label) == true){
      alert("You clicked to share")
    }
    else {
      alert("You did not share")
    }
  }
  
  
  render() {
    
    var OptionsList = this.listOptions.map(function(object,index){

      return <div key={index}  className="amp-list-item" onClick={this.clickHandler.bind(this, object, index)}>
              <button className={`amp-icon ${object.icon}`} ></button>
              <span>{object.label}</span>
             </div>
    }, this);
    
    
    return <div ref="element" className={`amp-share amp-panel ${this.className}`}>
            {OptionsList}
           </div>
  }
} 