import AMPControl from "./AMPControl.js"

export default class PlayPause extends AMPControl {
  
  constructor(props) {
    super(props)
    this.classList.add("amp-playpause")
  }
  
  onplaying() {
    this.setState({altText: this.player.l10n["MSG_PAUSE"]})
  }
  
  onpause() {
    this.setState({altText: this.player.l10n["MSG_PLAY"]})
  }
  
  onended() {
    this.setState({altText: this.player.l10n["MSG_REPLAY"]})
  }
  
  onClick() {
    const player = this.player
    switch (player.playState) {
      case "ended":
        player.currentTime = 0;
      case "ready":
      case "paused":
        player.play()
      break;
      
      default:
        player.pause()
      break;
    }
  }

} 