import AMPControl from "./AMPControl.js"

export default class Fullscreen extends AMPControl {
  
  constructor(props) {
    super(props)
    this.event = (document.onwebkitfullscreenchange !== undefined) ? "webkitfullscreenchange" : (document.onmozfullscreenchange !== undefined) ? "mozfullscreenchange" : "fullscreenchange"
    this.requestFullscreen = (this.player.container.requestFullscreen || this.player.container.webkitRequestFullscreen).bind(this.player.container)
    this.exitFullscreen = (document.exitFullscreen || document.webkitExitFullscreen).bind(document)
    this.fullscreenchange = this.fullscreenchange.bind(this)
    this.classList.add("amp-fullscreen")
  }
  
  onfullscreenchange(event) {
    const key = (event.detail) ? "MSG_EXIT_FULLSCREEN" : "MSG_ENTER_FULLSCREEN"
    this.setState({altText: this.player.l10n[key]})
  }
  
  get fullscreenElement() {
    return document.fullscreenElement || document.webkitFullscreenElement
  }
  
  fullscreenchange() {
    this.classList.update({"amp-full": (this.fullscreenElement != null)})
  }
  
  componentWillMount() {
    super.componentWillMount()
    document.addEventListener(this.event, this.fullscreenchange)
  }
  
  componentWillUnmount() {
    super.componentWillUnmount()
    document.removeEventListener(this.event, this.fullscreenchange)
  }
  
  onClick() {
    if (this.fullscreenElement == null) {
      this.player.enterFullScreen()
    } else {
      this.player.exitFullScreen()
    }
  }
} 