import Panel from "../Panel.js"

export default class FontSize extends Panel {
  
  constructor(props) {
    super(props)
    this.listOptions = [
                         {label:"70%", value: "Extra small"},
                         {label:"100%", value: "Small"},
                         {label:"150%", value: "Medium"},
                         {label:"200%", value: "Large"},
                         {label:"300%", value: "Extra large"}
                       ]
                       
    this.listOptions.map(function(object, index){ 
     if(object.label === props.captionsettings['fontSizeLabel']) {
       this.state.activeIndex = index
     }
    },this)    

  }
  
  clickHandler (object, index) {
    this.setState({activeIndex: index})
    this.props.settingsChange({"fontSize": { "label":object.label , "value":object.value}})
  }
  
  render() {
    
    var OptionsList = this.listOptions.map(function(object, index){

      return <div key={index} className="amp-list-item" onClick={this.clickHandler.bind(this, object, index)}>
              <button className={index === this.state.activeIndex ? 'amp-icon amp-list-item-selected':'amp-icon'}></button>
              <span>{object.label}</span>
             </div>
    }, this);
    
    return <div>
            <div className="amp-list-item amp-list-title" onClick={this.props.viewChange.bind(this, "options")}>
              <button className="amp-icon amp-icon-left"></button>
              <span>Font Size</span>
            </div>
            {OptionsList}
           </div>
  }
}
