import AMPControl from "./AMPControl.js"

export default class AMPPanelControl extends AMPControl {
  constructor(props) {
    super(props)
  }
  
  get panel() {
    return this.props.panel
  }
  
  get propsList() {
    const { panel, ...props } = super.propsList
    return props
  }
  
  onClick(event) {
    this.panel.toggle()
  }
}