import AMPClassList from "./AMPClassList.js"

export default class AMPComponent extends React.Component {
  
  constructor (props) {
    super(props)
    this.state = {}
    this.classList = new AMPClassList(this, this.props.classList)
  }
  
  get player() {
    return this.props.player
  }
  
  get config() {
    return this.props.config
  }
  
  get className() {
    let className = this.props.className || ""
    className += ` ${this.classList}`
    
    return className.trim()
  }
  
  get textContent() {
    return ""
  }
  
  get element() {
    return this.refs.element
  }
  
  get playerEventMap() {
    const events = []
    let key, type
    
    if (this.player == null)
      return events
    
    for (type of akamai.amp.Events.values) {
      key = `on${type}`
      if (typeof this[key] != "function")
        continue
      events.push({key: key, type: type})
    }
    
    return events
  }
  
  get propsList() {
    const { player, config, className, classList, ...props } = this.props
    return props
  }
  
  shouldComponentUpdate(nextProps, nextState) {
    let state = this.state
    let props = this.props
    
    if (nextState != null) {
      for (let key in nextState) {
        if (nextState[key] != state[key]) {
          return true
        }
      }
    }
    
    // TODO: Is this needed?
    if (nextProps != null) {
      for (let key in nextProps) {
        if (nextProps[key] != props[key]) {
          return true
        }
      }
    }
    
    return false
  }
  
  componentWillMount() {
    this.playerEventMap.forEach((event) => {
      this[event.key] = this[event.key].bind(this)
      this.player.addEventListener(event.type, this[event.key])
    }) 
  }
  
  componentWillUnmount() {
    this.playerEventMap.forEach((event) => {
      this.player.removeEventListener(event.type, this[event.key])
    })
  }
  
  render() {
    return <div ref="element" {...this.propsList} className={this.className}>{this.state.textContent}{this.props.children}</div>
  }
}