import AMPComponent from "./AMPComponent.js"

export default class AMPContainer extends AMPComponent {
  constructor(props) {
    super(props)
    this.state.components = []
  }
  
  addComponent(component) {
    let components = this.state.components.slice()
    components.push(component)
    this.setState({components})
  }
  
  removeComponent(component) {
    let components = this.state.components.filter((item) => item != component)
    this.setState({components})
  }
  
  render() {
    return (
      <div ref="element" {...this.propsList} className={this.className}>
        {this.props.children}
        {this.state.components}
      </div>
    )
  }
}