import AMPControl from "./AMPControl.js"

export default class PlaybackRate extends AMPControl {
  
  constructor(props) {
    super(props)
    this.rates = [
      "\u00BDx",
      "1x",
      "1\u00BDx",
      "2x"
    ]
    this.state.rate = 1
    this.state.textContent = this.rates[this.state.rate]
    this.classList.add("amp-playback-rate")
  }
  
  onClick(event) {
    let rate = this.state.rate + 1
    if (rate >= this.rates.length)
      rate = 0
    this.player.playbackRate = (rate + 1) * 0.5
    this.setState({rate: rate, textContent: this.rates[rate]})
  }
} 