import AMPControl from "./AMPControl.js"

export default class Mute extends AMPControl {
  
  constructor(props) {
    super(props)
    this.state.level = ""
    this.classList.add("amp-mute")
  }
  
  get className() {
    return `${super.className} ${this.state.level}`
  }
  
  onClick() {
    const player = this.player
    player.muted = !player.muted
  }
  
  onvolumechange() {
    const volume = this.player.volume
    let level = ""
    
    if (volume == 0) {
      level = "amp-muted"
    } else if (volume < 0.5) {
      level = "amp-low"
    }
    
    this.setState({level})
  }
} 