import AMPComponent from "./AMPComponent.js"
import Utils from "./Utils.js"

export default class CurrentTime extends AMPComponent {
  
  constructor(props) {
    super(props)
    this.state.currentTime = 0
  }
  
  ontimeupdate() {
    this.setState({currentTime: Math.round(this.player.currentTime)})
  }
  
  render() {
    return <span className="amp-current-time">{Utils.formatTimecode(this.state.currentTime, this.props.player.duration)}</span>
  }
  
}