import Panel from "../Panel.js"

export default class Home extends Panel {
  
  constructor(props) {
    super(props)
    this.listOptions = [{label:"Off"}]
  }
  
  clickHandler (object, index) {
    this.setState({activeIndex: index})
    if (!index) {
      return this.props.settingsChange({'showing':false});
    }
    this.props.settingsChange({"language": this.listOptions[index].label})
  }
  
  render() {
    this.listOptions.splice(1)
    for (var lang in this.props.languages) {
      this.listOptions.push({'label': this.props.languages[lang]})
    }
    
    var OptionsList = this.listOptions.map(function(object, index){

      return <div key={index}  className="amp-list-item" onClick={this.clickHandler.bind(this, object, index)}>
              <button className={index === this.state.activeIndex ? 'amp-icon amp-list-item-selected':'amp-icon'}></button>
              <span>{this.props.player.localization.getLanguageString(object.label) || object.label}</span>
             </div>
    }, this);
    
    
    return (
      <div ref="element" className="amp-captioning-home">
        <div>
          <span className="amp-list-highlight" onClick={this.props.gotoSettings.bind(this)}>
            <button className="amp-icon amp-icon-left"></button>
            <span>Subtitles/CC</span>
          </span>
          <button onClick={this.props.viewChange.bind(this, "options")} className="amp-option-button">Options</button>
        </div>
        
        {OptionsList}
      </div>
    );   
    
  }
}
