import AMPControl from "./AMPControl.js"
import AMPContainer from "./AMPContainer.js"
import Utils from "./Utils.js"

export default class Slider extends AMPControl {
  
  constructor(props) {
    super(props)
    this.state.value = 0
    this.state.time = 0
    this.increment = 0.1
    this.classList.add("amp-slider")
  }
  
  set value(value) {
    // TODO: Replace with akamai.amp.Utils.clamp
    value = akamai.amp.utils.Utils.clamp(value, 0, 1)
    
    if (this.value == value)
      return
    
    this.setState({value})
    this.valueChanged(value)
  }
  
  get value() {
    return this.state.value
  }
  
  get markers() {
    return this.refs.markers
  }
  
  onClick(event) {
    const offset = Utils.getElementOffset(this.refs.element)
    const pos = Utils.getEventPos(event)
    this.value = (pos.x - offset.left) / offset.width
  }
  
  onAction() {
    
  }
  
  onKeyPress(event) {
    const code = event.keyCode
    // left arrow	37
    // up arrow	38
    // right arrow	39
    // down arrow	40
    if (code == 39 || code == 38) {
      this.value += this.increment
    } else if (code == 37 || code == 40) {
      this.value -= this.increment
    }
  }
  
  valueChanged() {}
  
  render() {
    const value = `${Utils.round(this.value * 100, 2)}%`
    return (
      <div ref="element" {...this.propsList} 
          className={this.className} 
          tabIndex="0"
          title={this.state.altText}
          aria-label={this.state.altText}
          onMouseDown={(event) => this.onMouseDown(event)}
          onMouseUp={(event) => this.onMouseUp(event)}
          onFocus={(event) => this.onFocus(event)}
          onBlur={(event) => this.onBlur(event)}
          onKeyDown={(event) =>  this.onKeyPress(event)}
          onClick={(event) => this.onClick(event)}>
        <div className="amp-value" style={{width: value}}></div>
        <div className="amp-handle" style={{left: value}}></div>
        <AMPContainer ref="markers" className="amp-markers"></AMPContainer>
        <div className="amp-text" style={{left: value}}>{this.state.time}</div>
      </div>
    )
  }
  
}