import AMPComponent from "./AMPComponent.js"

export default class AMPControl extends AMPComponent {
  constructor(props) {
    super(props)
    this.classList.add("amp-icon")
    this.classList.add("amp-control")
    this.mouseFocus = false
    this.state.altText = this.altText
  }
  
  get propsList() {
    const { altText, ...props } = super.propsList
    return props
  }
  
  get altText() {
    if (this.player == null)
      return ""
    return this.player.l10n[this.props.altText]
  }
  
  onlanguagechange() {
    this.setState({altText: this.altText})
  }
  
  onMouseDown(event) {
    this.mouseFocus = true
  }
  
  onMouseUp(event) {
    this.mouseFocus = false
  }
  
  onFocus(event) {
    if (this.mouseFocus == true)
      return
      
    this.classList.update({"amp-focus": true})
  }
  
  onBlur(event) {
    if (this.mouseFocus == true)
      return
      
    this.classList.update({"amp-focus": false})
  }
  
  onKeyDown(event) {
    if (this.mouseFocus == true)
      return
      
    this.onKeyPress(event)
    
    if (event.type == "keypress" && (event.keyCode == 13 || event.keyCode == 32))
      this.onAction()
  }
  
  onAction() {
    this.onClick()
  }
  
  onKeyPress(event) {

  }
  
  onClick(event) {
    
  }
  
  render() {
    return (
      <button ref="element" {...this.propsList} 
          className={this.className} 
          tabIndex="0"
          title={this.state.altText}
          aria-label={this.state.altText}
          onMouseDown={(event) => this.onMouseDown(event)}
          onMouseUp={(event) => this.onMouseUp(event)}
          onFocus={(event) => this.onFocus(event)}
          onBlur={(event) => this.onBlur(event)}
          onKeyDown={(event) => this.onKeyDown(event)}
          onClick={(event) => this.onClick(event)}>
        {this.state.textContent}
        {this.props.children}
        {this.state.components}
      </button>
    )
  }
}