import Panel from "../Panel.js"

export default class Options extends Panel {
  
  constructor(props) {
    super(props)
    this.listOptions = [{label:"Font Family", key:"fontFamily", value: "Arial"},
                         {label:"Font Color", key:"fontColor", value: "Red"},
                         {label:"Font Size", key:"fontSize", value: "100%"},
                         {label:"Font Opacity", key:"fontOpacity", value: "0%"},
                         {label:"Background Color", key:"backgroundColor", value: "Black"},
                         {label:"Background Opacity", key:"backgroundOpacity", value: "0%"},
                         {label:"Window Color", key:"windowColor", value: "Red"},
                         {label:"Window Opacity", key:"windowOpacity", value: "0%"},
                         {label:"Character Edge Style", key:"edgeType", value: "None"},
                         {label:"Scroll", key:"scroll", value: "Pop-out"},
                        ]
  }
  
  
  render() {
    
    var OptionsList = this.listOptions.map(function(object, index){
      let parentKey = object.key.match(/(Color|Opacity|fontSize)/) ? object.key+"Label" : object.key
      return <div key={object.key} onClick={this.props.viewChange.bind(this, object.key)} className="amp-list-item">
                <span className="amp-label">{object.label}</span>
                <button className="amp-icon amp-icon-right amp-right"></button>
                <span className="amp-right">
                  {this.props.captionsettings[parentKey]}
                </span>
             </div>
    }, this);
    
    return <div className="amp-captioning-options">
            <div className="amp-list-item amp-list-title" onClick={this.props.viewChange.bind(this, "home")}>
              <button className="amp-icon amp-icon-left"></button>
              <span>Options</span>
            </div>
            {OptionsList}
           </div>
  }
}
